/**
 * Utility for attendance status display labels
 */
export const getStatusDisplayLabel = (status?: string | null): string => {
  if (!status) return '';
  const s = String(status).trim();
  if (s.toLowerCase() === 'pengganti') return 'Tugas';
  return s;
};

export const isSubstituteStatus = (status?: string | null): boolean => {
  if (!status) return false;
  return String(status).trim().toLowerCase() === 'pengganti';
};

export default getStatusDisplayLabel;
